.header {
  margin: 0 2.5rem;
  padding: 0.5rem 0;
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.headerLeft,
.headerRight {
  display: flex;
  align-items: center;
}

a > span {
  padding: 10px 10px;
  font-size: 1.3rem;
  color: #8595b6;
}

a > span:hover {
  color: white;
}

.headerIcon {
  display: inline-block;
  transition: transform 0.2s;
  position: relative;
  overflow: hidden;
  margin: 0.19rem;
  cursor: pointer;
  min-width: 80px;
  z-index: 0;
}

.headerIcon:hover {
  transform: scale(1.2);
  z-index: 1000;
}
.headerNav {
  display: inline-block;
  transition: transform 0.2s;
  position: relative;
  overflow: hidden;
  margin: 0.19rem;
  cursor: pointer;
  z-index: 0;
  margin-top: 20px;
 
}

.headerNav:hover {
  transform: scale(1.2);
  z-index: 1000;
}