.posterImage {
  height: 320px;
}
.posterImage > img {
  width: 100%;
  display: block;
  margin: auto;
}
.posterImgOver {
  padding: 5rem;
  width: 100%;
  opacity: 1;
  transition: opacity 3s;
}
.posterImgOver:hover {
  opacity: 1;
}
.posterTitle {
  font-weight: bold;
  font-size: 3rem;
  text-align: left;
}
.posterDate {
  text-align: left;
}
.posterOverview {
  font-size: 1rem;
  margin-top: 5px;
  text-align: left;
}
